import { Box, Divider, Grid } from "@mui/material";
import format from "date-fns-jalali/format";
import sortBy from "lodash/sortBy";
import { useOutletContext } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import { useAuth } from "../../../../auth";
import DataListBox from "../../../../components/DataListBox";
import Text from "../../../../components/Text";


const ProductInfoGridItem = ({ product, loading }) => {

    const { inject } = useAuth();

    return (
        <DataListBox
            title="عمومی"
            loading={loading}
            data={sortBy([
                { title: 'کد محصول', priority: 100, description: <Text>{product.code}</Text> },
                { title: 'شرح محصول', priority: 200, description: product.description ? <Text>{product.description}</Text> : '-' },
                { title: 'آخرین بروزرسانی', priority: 300, description: product.updatedAt ? <Text>{format(new Date(product.updatedAt), 'dd MMMM yyyy HH:mm')}</Text> : '-' },
                { title: 'ایجاد در', priority: 400, description: product.createdAt ? <Text>{format(new Date(product.createdAt), 'dd MMMM yyyy HH:mm')}</Text> : '-' },
                ...(inject('Products', 'ProductDetails.Overview.DataList', { product, loading }) || [])
            ], o => o.priority)}
        />
    )
}


const ProductOverview = props => {

    const { inject } = useAuth();

    const { product, loading } = useOutletContext();


    return (
        <Box sx={{ p: 2 }}>
            <Grid container columns={{ xs: 1, sm: 2, md: 3 }}>
                {[
                    <ProductInfoGridItem product={product} loading={loading} priority={100} {...props} />,
                    ...(inject('Products', 'ProductDetails.Overview.Grid', { product, loading }) || [])
                ].map((child, index) => (
                    <Grid item xs={1} key={index}>
                        {child}
                    </Grid>
                )).reduce((prev, curr) => ([prev, <Divider orientation="vertical" flexItem sx={{ mr: '-1px', display: { xs: 'none', sm: 'flex' } }} key={uuidv4()} />, curr]))}
            </Grid>
        </Box>
    )
}

export default ProductOverview;